import React, { Component } from 'react';
import Buscador from './components/Buscador';
import Resultado from './components/Resultado';
import './App.css'

class App extends Component {

  state = {
    termino : '', 
    imagenes : [],
    pagina: '',
    cargando: false,
    totalPagina: ''
  }

  scroll = () => {
    const elemento = document.querySelector('.jumbotron');
    elemento.scrollIntoView('smooth', 'start');
  }

  paginaAnterior = () => {
    // leer el state de la pagina actual
    let pagina = this.state.pagina;

    // Leer si la pagina es 1, ya no ir hacia atras
    if(pagina === 1) return null;

    // Resta uno a la pagina actual
    pagina -= 1;

    // agregar el cambio al state
    this.setState({
      pagina
    }, () => {
      this.consultarApi();
      this.scroll();
    });

    // console.log(pagina);

  }
  paginaSiguiente = () => {
    // leer el state de la pagina actual
    let pagina = this.state.pagina;

    // Sumar uno a la pagina actual
    pagina += 1;

    // agregar el cambio al state
    this.setState({
      pagina
    }, () => {
      this.consultarApi();
      this.scroll();
    });

    // console.log(pagina);
  }

  consultarApi = async () => {
    const termino = this.state.termino;
    const pagina = this.state.pagina;
    const url = `https://pixabay.com/api/?key=1732750-d45b5378879d1e877cd1d35a6&q=${termino}&per_page=30&page=${pagina}`;

    console.log(url);

    await fetch(url)
      .then(respuesta => {
        this.setState({
          cargando: true
        })
        return respuesta.json()
      } )
      .then(resultado => {
        console.log( resultado.totalHits )
        console.log( resultado.totalHits / 30)
        const totalPaginacion =  Math.ceil(resultado.totalHits / 30)
          return this.setState({ 
            imagenes : resultado.hits,
            cargando: false,
            totalPagina: totalPaginacion
          })
      } )
  }

  datosBusqueda = (termino) => {

    console.log(termino);
    this.setState({
      termino : termino,
      pagina : 1
    }, () => {
      this.consultarApi();
    })
  }
  

  render() {
    const cargando = this.state.cargando;
    let resultado;
    if( cargando ){
      resultado = <div className="spinner">
                    <div className="double-bounce1"></div>
                    <div className="double-bounce2"></div>
                  </div>
    }else {
      resultado = <Resultado 
                      imagenes={this.state.imagenes}
                      paginaAnterior={this.paginaAnterior}
                      paginaSiguiente={this.paginaSiguiente}
                      pagina={this.state.pagina}
                      totalPagina={this.state.totalPagina}
                  />
    }
    return (
      <div className="app container">
        <div className="jumbotron">
            <p className="lead text-center">Buscador de Imgenes </p>
            <Buscador
              datosBusqueda={this.datosBusqueda}
            />
        </div>
        <div className="row justify-content-center">
            {resultado}
        </div>
      </div>
    );
  }
}

export default App;



