import React, {Component} from 'react';

class Paginacion extends Component {

  mostrarAnterior = () => {
    const {pagina} = this.props;

    if(pagina === 1) return null;

    return (
      <button onClick={this.props.paginaAnterior} type="button" className="btn btn-info mr-1">  {this.props.pagina - 1} &larr; Anterior </button>
    )
  }

  mostrarSiguiente = () => {
    const {pagina, totalPagina} = this.props;

    if(pagina === totalPagina || totalPagina === 1) return null;

    return (
      <button onClick={this.props.paginaSiguiente} type="button" className="btn btn-info">Siguiente  &rarr; {this.props.pagina + 1} </button>
    )
  }
  render() {

      return (
        <div className="py-3">
              {this.mostrarAnterior()}
              <button  type="button" className="btn btn-info mr-1"> {this.props.pagina} </button>
              {this.mostrarSiguiente()}
              
        </div>
      );
  }
}

export default Paginacion;


